import React, { Component } from 'react';
import { fade, makeStyles } from '@material-ui/core/styles';
import _ from 'lodash';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import InputBase from '@material-ui/core/InputBase';
import SearchIcon from '@material-ui/icons/Search';
import Typography from '@material-ui/core/Typography';
import TransitionsModal from './TransitionsModal';

const useStyles = makeStyles(theme => ({
  grow: {
    flexGrow: 1,
  },
  appBar: {
    boxShadow: 'none',
  },
  toolbar: {
    display: 'flex',
    justifyContent: 'center',
    flexDirection: 'column',
    height: 128,
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 1),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 1),
    },
    // marginRight: theme.spacing(2),
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      // marginLeft: theme.spacing(3),
      width: 'auto',
    },
  },
  searchIcon: {
    width: theme.spacing(7),
    color: 'rgba(0, 0, 0, 0.54)',
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 7),
    color: 'rgba(0, 0, 0, 0.87)',
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: 420,
    },
  },
  h4: {
    padding: '20px 0 12px',
  },
}));

const PrimarySearchAppBar = () => {
  const classes = useStyles();
  return (
    <AppBar position="static" className={classes.appBar}>
      <Toolbar className={classes.toolbar}>
        <div className={classes.search}>
          <div className={classes.searchIcon}>
            <SearchIcon />
          </div>
          <InputBase
            id="searchBar"
            placeholder="Search foods…"
            classes={{
              root: classes.inputRoot,
              input: classes.inputInput,
            }}
            inputProps={{ 'aria-label': 'search' }}
            // onChange={onChange}
            // onChange={e => onChange(e)}
          />
        </div>
        <Typography variant="h4" className={classes.h4}>
          Today
        </Typography>
      </Toolbar>
    </AppBar>
  );
};

class MyAppBar extends Component {
  state = {
    modalVisible: false,
  };

  componentDidMount = () => {
    const { searchFood } = this.props;
    const searchBar = document.querySelector('#searchBar');
    console.warn(`%c ( ¯•ω•¯ ):  `, 'background: #2196F3; color: #000', searchBar);
    // searchBar.addEventListener('input', _.debounce(e => this.searchFood(e.target.value), 500));
    // searchBar.addEventListener('focus', e => console.warn('Focused'), true);
    searchBar.oninput = _.debounce(e => searchFood(e.target.value), 500);
    searchBar.onfocus = e => {
      console.warn('聚焦');
      this.setState({ modalVisible: true });
    };
    searchBar.onblur = e => {
      console.warn('失焦');
      this.setState({ modalVisible: false });
    };
  };

  // componentWillUnmount = () => {
  //   const searchBar = document.querySelector('#searchBar');
  //   searchBar.removeEventListener('input'); // TODO: FIXME:
  // };

  // onChange = _.debounce(search//FIXME:?, 500));

  render() {
    return (
      <>
        <PrimarySearchAppBar onChange={this.onChange} />
        {/* <TransitionsModal /> */}
      </>
    );
  }
}

export default MyAppBar;
